.SetUpContainer {
    display: grid;
    grid-template-columns: 20% 60% 20%;
    align-items: center;
}

.Middle {
    justify-self: center;
    text-align: center;
}

.ShipPicOne {
    width: 100%;
    max-width: 200px;
}
.ShipPicTwo {
    width: 100%;
    max-width: 300px;
}
.ShipPicThree {
    width: 100%;
    max-width: 400px;
}
.ShipPicFour {
    width: 100%;
    max-width: 500px;
}

.GridContainer {
    justify-self: center;
    display: grid;
    grid-template-columns: repeat(10, 50px);
    background-color: rgba(0, 128, 128, 0.241);
}

.Square {
    height: 50px;
    width: 50px;
    border: 1px solid black;
    cursor: pointer;
}

.SquareDisabled {
    height: 50px;
    width: 50px;
    border: 1px solid black;
    background-color: lightsalmon;
    cursor: not-allowed;
}
.SquarePlaced {
    height: 50px;
    width: 50px;
    border: 1px solid black;
    background-color: grey;
    cursor: not-allowed;
}

.Square:hover {
    background-color: lightgreen;
}

.SquareDisabled:hover {
    background-color: lightsalmon;
}

.AxisButton {
    width: 100px;
    height: 40px;
    border: 1px solid black;
    border-radius: 8px;
    color: black;
    background-color: transparent;
    font-size: 25px;
    cursor: pointer;
    justify-self: center;
}

.SubmitButton {
    width: 100px;
    height: 40px;
    border: 1px solid black;
    border-radius: 8px;
    color: black;
    background-color: transparent;
    font-size: 25px;
    cursor: pointer;
    justify-self: center;
}

.SubmitButton:disabled {
    cursor: not-allowed;
}
