.BattleContainer {
    position: absolute;
    background-color: thistle;
    top: 0%;
    left: 0%;
    width: 100%;
    min-height: 100%;
}

.BattleSplitter {
    width: 100%;
    text-align: center;
    display: grid;
    grid-template-columns: auto;
}

.FriendlyWaters {
    text-align: center;
    justify-self: center;
}

.FriendlyGrid {
    display: grid;
    grid-template-columns: repeat(10, 50px);
}

.FriendlyEmptySquare {
    height: 50px;
    width: 50px;
    border: 1px solid black;
}
.FriendlyShipOne {

    background-color: blue;
}
.FriendlyShipTwo {

    background-color: green;
}
.FriendlyShipThree {

    background-color: orange;
}
.FriendlyShipFour {

    background-color: purple;
}

.FriendlyShipHit {
    background-color: red;
}







.EnemyWaters {
    text-align: center;
    justify-self: center;
}

.EnemyDecorations {
    display: grid;
    grid-template-columns: auto auto auto;
    max-height: 520px;
}

.EnemyDecorations img {
    height: 100%;
    width: 100%;
    max-height: 500px;
    
}

.EnemyGrid {
    display: grid;
    grid-template-columns: repeat(10, 50px);
}

.EnemyEmptySquare {
    height: 50px;
    width: 50px;
    border: 1px solid black;
}
.EnemyShipOne {

    background-color: blue;
}
.EnemyShipTwo {

    background-color: green;
}
.EnemyShipThree {

    background-color: orange;
}
.EnemyShipFour {

    background-color: purple;
}

.EnemyShipHit {
    background-color: red;
}
